import wLoading from "@/components/w-loading/w-loading.vue";
import qs from 'qs'
export default {
	data() {
		return {
			nameInfo: null,
			headConl: false
		};
	},
	components: {
		wLoading
	},
	computed:{
		characterAll(){
			let arr = []
			if(this.nameInfo){
				let target=this.nameInfo.list
				for(let i=0;i<target.length;i++){
					arr.push(target[i].constellation_name_character.split(',').splice(0,4))
				}
			}
			return arr
		}
	},
	onLoad() {
		this.$nextTick(() => {
			this.$refs.loading.show();
		})

		uni.request({
			url: this.$store.state.baseUrl + '/api/child/list',
			method: 'POST',
			data: uni.getStorageSync('child_params'),
			header: {
				"Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
			},
			success: (res) => {
				if (res.data.code == 1) {
					this.nameInfo = res.data.data
					this.$refs.loading.close()
				} else {
					this.$refs.loading.close()
					uni.showModal({
						title: '提示',
						content: res.data.msg,
						showCancel: false,
						success: function(res) {
							uni.navigateBack()
						}
					});
				}
			}
		})
	},
	methods: {
		submit_pay(){
			uni.navigateTo({
				url: '../pay/pay'
			});
		},
		headControl() {
			this.headConl = !this.headConl
		},
		goDetail(obj){
			let params = {
				app_mchid: uni.getStorageSync('token'),
				name_id: obj.name_id,
				tk: obj.tk,
				order_forms: 2,
				app: 1
			}
			uni.navigateTo({
				url: '../child_name/child_name?' + qs.stringify(params)
			});
		}
	}
}
